<template>
  <section class="deposit-page">
    <Tools :tools="tools"/>
    <TextHeader>
      {{ wizardSteps[currentStep].title }} ({{ currentStep + 1 }}/{{ wizardSteps.length }})
    </TextHeader>
    <Wizard
      v-model="currentStep"
      :steps="wizardSteps"
    />
    <component
      :is="'Step' + (currentStep + 1)"
      @next="setCurrentStep"
    />
  </section>
</template>

<script>
import { computed, defineAsyncComponent, ref } from 'vue'
import { useI18n } from 'vue-i18n'
import TextHeader from '@/components/TextHeader'
import Wizard from '@/components/UIKit/Wizard/Wizard'
import Button from '@/components/UIKit/Button'
import Input from '@/components/UIKit/Input'
import Tools from '@/components/Tools/Tools'
import Icon from '@/components/Icon/Icon'

export default {
  name: 'DepositPage',
  components: {
    Step1: defineAsyncComponent(() => import('@/views/DepositPage/Steps/Step1')),
    Step2: defineAsyncComponent(() => import('@/views/DepositPage/Steps/Step2')),
    Step3: defineAsyncComponent(() => import('@/views/DepositPage/Steps/Step3')),
    Icon,
    Button,
    Input,
    Wizard,
    TextHeader,
    Tools
  },
  setup () {
    const { t } = useI18n()
    const currentStep = ref(0)
    const email = ref('')
    const emailPlaceholder = ref('notifyme@myemail.com')

    const wizardSteps = computed(() => ([
      { title: t('depositPage.awaitingDeposit'), id: 1, color: '246, 226, 102' },
      { title: t('depositPage.processing'), id: 2, color: '212, 131, 67' },
      { title: t('depositPage.sending'), id: 3, color: '68, 190, 73' }
    ]))

    const tools = [
      {
        icon: 'copy',
        copyUrl: true
      }
    ]

    const setCurrentStep = (step) => {
      currentStep.value = step
    }

    return {
      t,
      wizardSteps,
      currentStep,
      email,
      emailPlaceholder,
      tools,
      setCurrentStep
    }
  }
}
</script>

<style lang="scss">
.deposit-page {
  padding: 40px 60px 70px 60px;
}
</style>
